var QQMapWX = require('../../commen/qqmap-wx-jssdk.js');
// var qqmapsdk;
import {
	addressData
} from '../../commen/spot.js'
export default {
	name: 'search',
	data() {
		return {
			inputValue:'',
			show: false,
			address: '',
			addressInfo: [],
			commit:'',
			citys: [{
					city: '昆明市'
				},
				{
					city: '大理白族自治州'
				},
				{
					city: '北京市'
				},
				{
					city: '上海市'
				},
				{
					city: '广州市'
				},
				{
					city: '深圳市'
				},
				{
					city: '杭州市'
				}
			]
		}
	},
	mounted() {
		this.addRess()
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		seekBack(lacotion){
			this.$store.commit(this.commit, lacotion)
			this.back()
		},
		theCity() {
			console.log(this.address)
			this.$store.commit(this.commit, this.address)
			this.back()
		},
		getCity(city) {
			console.log(city)
			this.$store.commit(this.commit, city)
			this.back()
		},
		search() {
			this.show = true
		},
		reverseShow() {
			this.show = false
			this.inputValue = ''
			this.addressInfo = []
		},
		addRess() {
			addressData().then(res => {
					this.address = res.result.address_component.city
					console.log(this.address)
				})
				.catch(err => {
					console.log(err)
				})
		},
		searchInput(e) {
			var qqmapsdk = new QQMapWX({
				key: 'F3CBZ-WJHC6-EAVSX-EVDO6-CUTJV-3ZFGN'
			})
			qqmapsdk.getSuggestion({
				keyword:e.detail.value,
				//filter:'category=景点',
				success:res=>{
					let arrData = res.data
					let city = arrData[0].city
					let titleData = arrData.map(item=>{
						return item.title
					})
					let rezult = [city,...titleData]
					this.addressInfo = rezult
					console.log(titleData)
				},
				fail:err=>{
					console.log(err)
				}
			})
		},
		routeFrom(){
			let page = getCurrentPages()
			let prePage = page[page.length - 2].route
			console.log(prePage)
			if(prePage === 'pages/upload/upload'){
				this.commit = 'fromUploadCity'
			}else{
				this.commit = 'fromStrategyCity'
			}
		}
	},
	onLoad(){
		this.routeFrom()
	}
}
